*,
*::before,
*::after {
    box-sizing: border-box;
}

html {
    font-size: 10px;
}

body {
    margin: 0;
    padding: 0;

    color: var(--color-text);
    font-family: var(--font-family-text);
    font-size: var(--size-base);

    background-color: var(--color-body);
}

@media (min-width: 800px) {
    body::after {
        content: '';

        display: block;
        height: var(--height-bottom-line);

        background-image: linear-gradient(to left, var(--color-turquoise-2) 0%, var(--color-purple-2) 50%, var(--color-turquoise-2) 100%);
        background-repeat: repeat-x;
        background-position: 0 100%;
        background-size: 200% 100%;

        animation: bodyAfterBackground 60s linear infinite;
    }

    @keyframes bodyAfterBackground {
        0% {
            background-position: 200% 100%;
        }

        100% {
            background-position: 0% 100%;
        }
    }
}

.sr-only {
    position: absolute;

    overflow: hidden;
    clip: rect(0, 0, 0, 0);

    width: 1px;
    height: 1px;
}

.js-only {
    display: none;
}

@media (max-width: 800px) {
    .no-mobile {
        display: none;
    }
}

@media (min-width: 800px) {
    .only-mobile {
        display: none;
    }
}

.accessibility-nav {
    position: absolute;

    overflow: hidden;
    clip: rect(0, 0, 0, 0);

    width: 1px;
    height: 1px;
}

.accessibility-nav:focus {
    top: 0.75rem;
    left: 2rem;

    clip: auto;

    width: auto;
    height: auto;
}

hr {
    width: 50%;
    height: 0.3rem;
    margin: var(--space-larger) auto;

    background: linear-gradient(to right, var(--color-turquoise-2), var(--color-turquoise-4), var(--color-turquoise-2));
    border: none;
    border-radius: 2em;
}

.turbolinks-progress-bar {
    position: fixed;
    top: 0;

    height: 0.5rem;

    background: linear-gradient(to right, var(--color-turquoise-5), var(--color-purple-5));
}
